.process {
  width: 80%;
  position: relative;
  left: 10%;
  display: flex;
  justify-content: space-around;

  &__text {
    position: relative;
    top: 200px;
  }

  &__label {
    font-weight: bold;
    font-size: 13px;
    letter-spacing: 3px;
    color: #c0cdd7;
    text-transform: uppercase;
  }

  &__name {
    font-weight: 600;
    font-size: 32px;
    color: $main-text;
  }

  &__description {
    color: #6c788b;
  }

  &__info {
    color: #6c788b;
  }

  &__content {
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(2, 264px);
    grid-gap: 30px;
    z-index: 5;
  }

  &__block {
    width: 264px;
    height: 264px;
    background-color: #fff;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    box-sizing: border-box;
    position: relative;
    padding: 5%;
    z-index: 5;

    &--down {
      margin-top: 200px;
    }

    &--up {
      margin-top: -200px;
    }
  }

  &__number {
    border-radius: 5px;
    width: 46px;
    height: 24px;
    background-color: 0.1;
    text-align: center;
  }

  &__learn {
    color: $main-text;
    font-weight: 600;
    font-size: 15px;
  }

  &__button {
    padding: 16px;
    border-radius: 50%;
    border: none;
    margin-right: 10px;
  }
}

.number--first {
  background: rgba(86, 204, 242, 0.1);
  color: #56ccf2;
}

.number--second {
  background-color: rgba(252, 201, 29, 0.1);
  color: rgb(252, 201, 29);
}

.number--third {
  background-color: rgba(21, 216, 80, 0.1);
  color: rgb(21, 216, 80);
}

.number--fourth {
  background-color: rgba(243, 99, 99, 0.1);
  color: rgb(243, 99, 99);
}

.button--first {
  background: rgb(86, 204, 242);
}

.button--second {
  background-color: rgb(252, 201, 29);
}

.button--third {
  background-color: rgb(21, 216, 80);
}

.button--fourth {
  background-color: rgb(243, 99, 99);
}

@media screen and (max-width: 900px) {
  .process {
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    left: 25px;
    &__text {
      position: relative;
      top: 20px;
    }
    &__content {
      margin-top: -100px;
      grid-gap: 10px;
      grid-template-columns: repeat(1, 264px);
      z-index: 5;
      margin-left: -15px;
    }
  }
}
